import React, { useState } from 'react'

//materail-ui
import Divider from '@material-ui/core/Divider';
import AddIcon from '@material-ui/icons/Add';
import RemoveIcon from '@material-ui/icons/Remove';
import Slider from '@material-ui/core/Slider';

//Componnents
import Item from '../Components/Item'
import ItemInShop from '../Components/ItemInShop'
import ItemInCart from '../Components/ItemInCart';

//Style
import '../Style/Shop.css'



export default function Shop() {

    let allItems = [redShirt, blueShirt, yellowShirt, redPents, bluPants, yellowPents, redHat, blueHat,yellowHat]

    const [iconColl, SetIconColl] = useState(icons.Add)
    const [iconPrice, SetIconPrice] = useState(icons.Add)
    const [iconColor, SetIconColor] = useState(icons.Add)
    const [collection, SetCollection] = useState([])
    const [value, setValue] = useState([0,50]);
    const [enableSlider, SetenableSlider] = useState(false)
    const [enableColor, SetEnableColor] = useState(false)
    const [color,SetColor] = useState('')
    const [data, SetData] = useState(allItems)
    const [type, SetType] = useState('All');
   


    const handleChange = (event,newValue) => {
        setValue(newValue);
        console.log(allItems);
        if (color !== '' && type !== 'All') {
            let newData = allItems.filter(d=> (d.price >= value[0] && d.price <= value[1]) && d.color === color && d.type === type)
            SetData(newData)
        }
        else if (color !== '') {
            let newData = allItems.filter(d=> (d.price >= value[0] && d.price <= value[1]) && d.color === color)
            SetData(newData)
        }
        else if (type !== 'All') {
            let newData = allItems.filter(d=> (d.price >= value[0] && d.price <= value[1]) && d.type === type )
            SetData(newData)
        }else { let newData = allItems.filter(d=> d.price >= value[0] && d.price <= value[1] )
            SetData(newData) }

         
    };

    const handleClickColl = () => {
        if (iconColl === icons.Add) {
            SetIconColl(icons.Remove)
            SetCollection(arr)

        } else {
            SetIconColl(icons.Add)
            SetCollection([])
        }

    }

    const handleClickPrice = () => {
        SetenableSlider(!enableSlider)
        if (iconPrice === icons.Add) {
            SetIconPrice(icons.Remove)
        } else {
            SetIconPrice(icons.Add)
        }
    }

    const handleClickColor = () =>{
        SetEnableColor(!enableColor)
        if (iconColor === icons.Add) {
            SetIconColor(icons.Remove)

        } else {
            SetIconColor(icons.Add)
        }
    }

    const FilterCollection = (t) => {
        SetType(t);
        setValue([0,50]);
        SetColor('')

        if (t === 'All') {
            SetData(allItems)
        }else{
            let newData  = allItems.filter(d=> d.type === t)
            SetData(newData)
        }
    }

    const FilterColor = (color) => {
        SetColor(color)
        if (color === '') {
            SetData(allItems)
        }else if (type !== 'All') {
            let newData = allItems.filter(d=> d.color === color  && d.type === type )
            SetData(newData)
        }
        else{
            let newData = allItems.filter(d=> d.color === color   )
            SetData(newData)  
        }
    }


    return (
        <div className="container">
            <div className="header">
                <h1>My Shop</h1>
            </div>
            <div className="content">
                <div className="aside" >
                    <h2 className="title">Filter By </h2>
                    <Divider />
                    <div className="collection">
                        <button className='btn-add-remove' onClick={handleClickColl} >
                            Collection
                        {iconColl}
                        </button>
                        {
                            collection.map((c, index) =>
                                <button className='btn-add-remove' key={index} onClick={()=>FilterCollection(c.value)}>
                                    {c.value} <br />
                                </button>

                            )
                        }
                    </div>
                    <Divider />
                    <div className="price">
                        <button className="btn-add-remove" onClick={handleClickPrice}>
                            Price
                            {iconPrice}
                        </button>
                        {enableSlider &&
                            <span>
                                <Slider
                                    style={{ width: 200, color: 'darkgray' }}
                                    value={value}
                                    onChange={handleChange}
                                    valueLabelDisplay="auto"
                                    max={50}

                                />
                                <br/>
                                {`${value[0]}$ - ${value[1]}$`}
                            </span>}
                    </div>
                    <Divider />
                    <div className="color">
                    <button className="btn-add-remove" onClick={handleClickColor}>
                        {color === '' ? 'Color' : 'Color: ' } {color} 
                            {iconColor}
                        </button>
                        {enableColor && <sapn style={{float:'left'}}>
                            <span className="dot" style={{backgroundColor:'yellow'}} onClick={()=>FilterColor('Yellow')}></span>
                            <span className="dot" style={{backgroundColor:'blue'}}  onClick={()=>FilterColor('Blue')}></span>
                            <span className="dot" style={{backgroundColor:'red'}} onClick={()=>FilterColor('Red')}></span>
                            <span className="dot" style={{backgroundColor:'white'}} onClick={()=>FilterColor('')}></span>
                        </sapn>

                        }

                    </div>
                    <div className="ItemInCart">
                    
                        <ItemInCart/>
                    </div>
                </div>
                <div className="mainContent" >
                    <ItemInShop data={data}/>

                </div>
            </div>


            
        </div>
    )
}
const icons = {
    Add: <AddIcon />,
    Remove: <RemoveIcon />
}
const arr = [{ value:'All'}, { value:'Shirts'}, { value:'Pents'}, { value:'Hats'}]

     let redShirt =  new Item(1, "Red Shirt", 30, 'Red','Shirts', "data:image/jpeg;base64,");
    let blueShirt =  new Item(2, 'Blue Shirt',50,'Blue','Shirts', "data:image/jpeg;base64,");
     let yellowShirt =  new Item(3, 'Yellow Shirt', 40, 'Yellow','Shirts', "data:image/jpeg;base64,");
     let redPents =  new Item(4,'Red Pents', 25,'Red','Pents', "data:image/jpeg;base64,")
    let bluPants =  new Item(5,"Blue Pents",20,'Blue','Pents', "data:image/jpeg;base64," );
    let yellowPents = new Item(6, "Yellow Pents", 45,'Yellow','Pents', "data:image/jpeg;base64," );
    let redHat = new Item(7, "Red Hat", 15,'Red','Hats', "https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcQ51bXpRfQKT45js3wGuWkDKM4BhxpUZI9csA&usqp=CAU" );
     let blueHat = new Item(8,"Blue Hat", 10,'Blue','Hats', "data:image/jpeg;base64,");
    let yellowHat = new Item (9, "Yellow Hat",20,'Yellow','Hats',"data:image/jpeg;base64," )







